.ng2-c-button-group {
  display: block;
  margin-bottom: 15px;
  position: relative;
  text-align: center; }

.ng2-c-button-group__list {
  display: flex;
  flex-direction: row;
  justify-content: center; }

.ng2-c-button-group-item {
  border: 1px solid #28648a;
  border-left: 0;
  box-sizing: border-box;
  color: #28648a;
  display: inline-block;
  min-width: 160px; }
  .ng2-c-button-group-item:first-child {
    border-left: 1px solid #28648a;
    border-radius: 4px 0 0 4px; }
  .ng2-c-button-group-item:last-child {
    border-radius: 0 4px 4px 0; }

.ng2-c-button-group-item--active {
  background-color: rgba(40, 100, 138, 0.2); }

.ng2-c-button-group-item__content {
  align-items: center;
